.Auth {
  margin: 20px auto;
  width: 80%;
  text-align: center;
  box-shadow: 0 2px 3px #ccc;
  border: 1px solid #eee;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
}

.ControlGroup {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
}

@media (min-width: 600px) {
  .Auth {
    width: 500px;
  }
}
